import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import DataTable, { createTheme } from 'react-data-table-component';
import { postStartLoading, postSetActive} from "../../actions/post";
import moment from 'moment';
import Modal from 'react-modal';

createTheme('solarized', {
    text: {
        primary: '#ffffff',
        secondary: '#ffffff',
    },
    background: {
        default: '#4a484d',
    },
    context: {
        background: '#cb4b16',
        text: '#FFFFFF',
    },
    divider: {
        default: '#073642',
    },
    action: {
        button: 'rgba(0,0,0,.54)',
        hover: 'rgba(0,0,0,.08)',
        disabled: 'rgba(0,0,0,.12)',
    },
});

const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)'
    }
};

const now = moment().minutes(0).seconds(0).add(1, 'hours'); // 3:00:00
const nowPlus1 = now.clone().add(1, 'hours');

const initEvent = {
    titulo: '',
    contenido: '',
    fecha: now.toDate(),
    multimedia: '',
    usuario: '',
}

export const DashboardPostsResume = () => {

    const dispatch = useDispatch();
   

    const { posts, activePost } = useSelector(state => state.post);
    const [formValues, setFormValues] = useState(initEvent);
    const [modalPublicaciones, setmodalPublicaciones] = useState(false);
    //Datos MODAL EVENTO
    const { contenido, titulo, multimedia, fecha, usuario } = formValues;

    useEffect(() => {
        dispatch(postStartLoading());

    }, [dispatch])
    useEffect(() => {
        if (activePost) {
            setFormValues(activePost);
        } else {
            setFormValues(initEvent);
        }
    }, [activePost, setFormValues])
    //DATOS TABLA
    const columnsPublicaciones = [
        {
            name: 'Titulo',
            selector: 'titulo',

        },
        {
            name: 'Fecha',
            selector: 'fecha',

        },
    ];


    //MODAL
    const handleInfoEvento = (evento) => {
        dispatch(postSetActive(evento));
        setmodalPublicaciones(true);
        
        
    }

    const closeModalEvento = () => {
        // TODO: cerrar el modal
        setmodalPublicaciones(false);
    }

    return (
        <div className="dashboard__content-resume-left">
            <div className="dashboard__content-resume-left-list">
                <DataTable
                    title="Publicaciones"
                    columns={columnsPublicaciones}
                    data={posts}
                    theme="solarized"
                    pagination="true"
                    paginationPerPage={3}
                    onRowClicked={handleInfoEvento}
                />
                <Modal
                    isOpen={modalPublicaciones}
                    //   onAfterOpen={afterOpenModal}
                    onRequestClose={closeModalEvento}
                    style={customStyles}
                    closeTimeoutMS={200}
                    className="modal modal-calendarmodal-ysm"
                    overlayClassName="modal-fondo"
                >
                    <div className="header-calendarmodal-ysm">
                        <div className="titulo-calendarmodal-ysm">
                            {titulo}
                        </div>
                    </div>
                    <div className="subheader-calendarmodal-ysm">
                        <div className="fechainicio-subheader-ysm">
                           Fecha de creacion:
                        </div>
                        <div className="fechafin-subheader-ysm">
                        {moment(fecha).format("DD-MM-yy")}
                        </div>
                    </div>
                    <div className="body-calendarmodal-ysm" dangerouslySetInnerHTML={{ __html: contenido }}>
                       
                    </div>
                    <div className="footer-calendarmodal-ysm">
                        <div className="link-footer-ysm">

                        </div>
                    </div>
                </Modal>
            </div>
        </div>
    )
}
