import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { dashboardCD, dashboardUsersCount, dashboardUOTW, dashboardUsersOnline } from "../../actions/dashboard";
import {Bar,Pie} from 'react-chartjs-2';
import moment from "moment";

export const DashboardReports = () => {

    const dispatch = useDispatch();
    useEffect(() => {
    dispatch(dashboardUOTW());
      dispatch(dashboardUsersOnline());
      dispatch(dashboardCD());
    }, [dispatch])
    var date = moment();
    const hoy = moment(date).format("DD-MM-YYYY")
    const date2 = moment(date).subtract(1, 'days');
    const date3 = moment(date).subtract(2, 'days');
    const date4 = moment(date).subtract(3, 'days');
    const date5 = moment(date).subtract(4, 'days');
    const date6 = moment(date).subtract(5, 'days');
    const date7 = moment(date).subtract(6, 'days');
    const hoy2 = moment(date2).format("DD-MM-YYYY")
    const hoy3 = moment(date3).format("DD-MM-YYYY")
    const hoy4 = moment(date4).format("DD-MM-YYYY")
    const hoy5 = moment(date5).format("DD-MM-YYYY")
    const hoy6 = moment(date6).format("DD-MM-YYYY")
    const hoy7 = moment(date7).format("DD-MM-YYYY")
    const {dashboard, usuariosoffline, conexionesdiarias} = useSelector(state => state.dashboard);
    const ususariosNoConexiones = dashboard.length - usuariosoffline.length
    console.log("Usuarios Offline",conexionesdiarias.dia1);
    return (
        <div className="dashboard__content-report">
          <div className="dashboard__content-repot-left">
            <Bar
            data={{
                labels:[hoy7,hoy6,hoy5,hoy4,hoy3,hoy2, hoy],
                datasets:[{
                    label:'Conexiones',
                    data:[conexionesdiarias.dia1,conexionesdiarias.dia2,conexionesdiarias.dia3,conexionesdiarias.dia4,conexionesdiarias.dia5,conexionesdiarias.dia6,conexionesdiarias.dia7],
                    backgroundColor:'red',
                    color:'white'
                }]

            }}
     
            options={{
                scales:{
                    xAxes:[
                        {
        
                                labelString:"Dias",
                                display:true,
                                fontColor:'white',
                            }

                    ],
                    yAxes:[
                        {
                            scales:{
                                labelString:'Porcentaje',
                                display:true,
                                fontColor:'white',
                            },
                            ticks:{
                                beginAtZero:true,
                                fontColor:'white',
                            }
                        }
                    ],
                },
                layout: {
                    padding: 0,
   
                },
                responsive: true,
                maintainAspectRatio: false,
                
            }}
            />
          </div>
          <div className="dashboard__content-repot-right">
            <Pie
            data={{
                labels:['Conectados de la semana','Desconectados de la semana'],
                datasets:[{
                    label:'Conexiones',
                    data:[usuariosoffline.length,ususariosNoConexiones],
                    backgroundColor:['green','orange'],
                }]

            }}
            options={{
                layout: {
                    padding: 0,
      
                },
                responsive: true,
                maintainAspectRatio: false,
            }}

     
     
            />
          </div>
        </div>
    )
}
