import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import DataTable, { createTheme } from 'react-data-table-component';
import { DashboardResume } from "./DashboardResume";
import { uiOpenModal } from "../../actions/ui";
import {
    eventsSetActive,
    eventsStartLoading,
} from "../../actions/eventos";

createTheme('solarized', {
    text: {
        primary: '#ffffff',
        secondary: '#ffffff',
    },
    background: {
        default: '#4a484d',
    },
    context: {
        background: '#cb4b16',
        text: '#FFFFFF',
    },
    divider: {
        default: '#073642',
    },
    action: {
        button: 'rgba(0,0,0,.54)',
        hover: 'rgba(0,0,0,.08)',
        disabled: 'rgba(0,0,0,.12)',
    },
});

export const DashboardEventsResume = () => {

    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(eventsStartLoading());
    }, [dispatch])

    const { events } = useSelector(state => state.events);

    const columnsEventos = [

        {
            name: 'Titulo',
            selector: 'titulo',
        },
        {
            name: 'Descripcion',
            selector: 'descripcion',
        },
    ];

    const handleInfoEvento = (evento) => {

        dispatch(eventsSetActive(evento));
        dispatch(uiOpenModal());

    }

    return (
        <div className="dashboard__content-resume-right">
            <div className="dashboard__content-resume-right-list">
                <DataTable
                    title="Eventos"
                    columns={columnsEventos}
                    data={events}
                    theme="solarized"
                    pagination="true"
                    paginationPerPage={3}
                    onRowClicked={handleInfoEvento}
                />
                <DashboardResume />
            </div>
        </div>
    )
}
